import Vue from 'vue'
import Router from 'vue-router'

import Vuex from 'vuex'
import Buycar from '../components/one/buycar'
import Foot from '../components/one/foot'
import First from '../components/one/first'

// import Buycar from '../components/one/buycar'

//@:src
//@:src
import Sweet from '@/components/one/sweet'
import Xiangqing from '@/components/one/xiangqing'
import Eat from '@/components/one/eat'
import Neirongo from '@/components/one/neirongo'
import Neirongt from '@/components/one/neirongt'
import Makesure from '@/components/one/makesure'
import MY from '../components/MY/MY'
import Msite from '../page/msite/msite'
import Download from '../page/download/download'
import Service from '../page/service/service'
import QuestionDetail from '../page/service/children/questionDetail'
import Profile from '../page/profile/profile'
import Setusername from '../page/profile/children/setusername'
import Balance from '../page/balance/balance'
import Benefit from '../page/benefit/benefit'
import Points from '../page/points/points'
import SouSuo from '@/components/ZMY/sousuo'
import Dingwei from '@/components/ZMY/dingwei'
import DengLu from '@/components/ZMY/denglu'
import Dingdan from '@/components/ZMY/dingdan'
import Change from '@/components/ZMY/change'
import Dingwei2 from '@/components/ZMY/dingwei2'
import Vipcard from '../page/vipcard/vip'
import Detail from '../page/points/children/detail'
import balDetail from '../page/balance/children/detail'
import Daijin from '../page/benefit/children/daijin'
import HbDescription from '../page/benefit/children/hbDescription'
import History1 from '../page/benefit/children/history1'
import History2 from '../page/benefit/children/history2'
import History3 from '../page/benefit/children/history3'

Vue.use(Router)
Vue.use(Vuex)


export default new Router({
  routes: [
    {
      path: '/',  //
      name:"dingwei",
      component: Dingwei,
    },
    {
      path: '/foot',    //
      component: Foot,
    },
    {
      path: '/bc',    //
      component: Buycar,
    },
    {
      path: '/first',      //
      // name:'first',
      component: First,
    },{ 
      path: '/sweet',      //   
      name:"sweet", 
      component: Sweet,
    },{
      path: '/eat',     //
      name:"eat",
      component: Eat,
    },{
      path: '/neirongo/:id',    //  
      name:"neirongo",
      component: Neirongo,
    },{
      path: '/neirongt/:id',    //  
      name:"neirongt",
      component: Neirongt,
    }, {
      path: '/xiangqing/:id',       //
      name:"xiangqing",
      component: Xiangqing,
    },{
      path: '/makesure',       //
      name:"makesure",
      component: Makesure,
    },{
      path: '/my',      //
      name: 'my',     
      component: MY
    }, {
      path: '/msite',
      name: 'msite',
      component: Msite
    },{
      path: '/download',  //app
      name:'download',
      component: Download
    },{
      path: '/service',//
      name:'service',
      component: Service,
    },{
      path: '/service/questionDetail', //
      component: QuestionDetail,
    },{
      path: '/my/profile', //
      component: Profile,
    }, {
      path: '/my/profile/setusername', //
      component: Setusername,
    },{
      path: '/my/balance', //
      component: Balance,
    },{
      path: '/my/balance/detail', //
      component: balDetail,
    },{
      path: '/my/points', //
      component: Points,
    },{
      path: '/my/points/detail', //
      component: Detail,
    },{
      path: '/my/benefit/history1', //1
      component: History1,
    },{
      path: '/my/benefit/history2', //2
      component: History2,
    },{
      path: '/my/benefit/history3', //3
      component: History3,
    },{
      path: '/my/benefit', //
      component: Benefit,
    },{
      path: '/my/benefit/daijin', //
      component: Daijin,
    },{
      path: '/my/benefit/hbDescription', //
      component: HbDescription,
    },{
      path: '/my/vipcard', //
      component: Vipcard,
    },{
      path: '/sousuo',    //
      name: 'SouSuo',
      component: SouSuo
    },{
      path: '/dingwei',   //
      name: 'dingwei',
      component: Dingwei
    },{
      path: '/denglu',   //
      name: 'denglu',
      component: DengLu
    },{
      path: '/dingdan', //
      name: 'dingdan',
      component: Dingdan
    },{
      path: '/change',
      name: 'change',
      component: Change
    },{
      path: '/dingwei2/:id',
      name: 'dingwei2',
      component: Dingwei2
    }

  ]  
})



