import Vue from 'vue'
//vuex 
import Vuex from 'vuex';
//state
import state from './state';
//getters
import {getters} from './getters';
//mutations
import mutations from './mutations';
//actions
import actions from './actions';


//vuex
Vue.use(Vuex);

//vuex
const store = new Vuex.Store({
    //data
    state,
    //computedstate
    getters,
    //methods
    //   action
    mutations,
    // Action  mutation
    // Action  mutation
    // Action 
    actions,
  
  })
  // alert(store.state.count);


  //
  export default store;


